import React, { useState, useRef, useContext, useEffect } from "react";
import { useClickOutside } from "./useClickOutside";
import "../styles/Drop.css";
import "../assets/images/genshin.jpg";
import { ADMIN_ROUTE, SHOP_ROUTE } from "../utils/consts";
import { Context } from "../index.js";
import { fetchDropInfo } from "../http/userAPI.js";

function Drop() {
    const { user } = useContext(Context);
    const [userInfo, setUserInfo] = useState({ avatar: "" });

    useEffect(() => {
        fetchDropInfo(4).then((data) => setUserInfo(data));
    }, []);

    //   
    const logOut = () => {
        localStorage.removeItem("token");
        user.setUser({});
        user.setIsAuth(false);
    };

    //    

    const [isOpen, setOpen] = useState(false);
    const menuRef = useRef(null);
    useClickOutside(menuRef, () => {
        if (isOpen) setTimeout(() => setOpen(false), 50);
    });

    return (
        <div className="social-icon">
            <button className="menu-button" onClick={() => setOpen(!isOpen)}>
                {userInfo.avatar && (
                    <img src={userInfo.avatar} alt="User avatar" />
                )}
            </button>
            <nav className={`menu ${isOpen ? "active" : ""}`} ref={menuRef}>
                <ul className="menu__list">
                    <li className="menu__item"></li>
                    <li className="menu__item"></li>
                    <li className="menu__item">
                        <a
                            style={{ display: "block", width: "100%" }}
                            href={ADMIN_ROUTE}
                        >
                            
                        </a>
                    </li>
                    <li className="menu__item">
                        <a
                            style={{ display: "block", width: "100%" }}
                            onClick={() => logOut()}
                            href={SHOP_ROUTE}
                        >
                            
                        </a>
                    </li>
                </ul>
            </nav>
        </div>
    );
}

export default Drop;
