import { makeAutoObservable } from "mobx";

export default class UserStore {
    constructor() {
        this._isAuth = false;
        this._user = {};
        makeAutoObservable(this);

        const storedAuth = localStorage.getItem("isAuth");
        if (storedAuth) {
            this._isAuth = JSON.parse(storedAuth);
        }
    }

    setIsAuth(bool) {
        this._isAuth = bool;
        localStorage.setItem("isAuth", JSON.stringify(bool));
    }

    setUser(user) {
        this._user = user;
    }

    get isAuth() {
        return this._isAuth;
    }

    get user() {
        return this._user;
    }
}
