import { $authHost, $host } from "./index";
import { jwtDecode } from "jwt-decode";

const checkToken = () => {
    const token = localStorage.getItem("token");
    if (token) {
        return jwtDecode(token);
    } else {
        return null;
    }
};

export const checkAuthOnReload = async () => {
    const tokenData = checkToken();
    if (tokenData) {
        return tokenData;
    } else {
        const { data } = await $authHost.get("api/user/auth");
        localStorage.setItem("token", data.token);
        return jwtDecode(data.token);
    }
};

export const registration = async (login, email, password) => {
    const { data } = await $host.post("api/user/registration", {
        login,
        email,
        password,
        role: 1,
    });
    localStorage.setItem("token", data.token);

    return jwtDecode(data.token);
};

export const login = async (email, password) => {
    const { data } = await $host.post("api/user/login", { email, password });
    localStorage.setItem("token", data.token);
    return jwtDecode(data.token);
};

export const check = async () => {
    const { data } = await $authHost.get("api/user/auth");
    localStorage.setItem("token", data.token);

    return jwtDecode(data.token);
};

export const fetchDropInfo = async (id, login, avatar) => {
    const { data } = await $host.get(`api/user/${id}`, { login, avatar });
    return data;
};
